import React from 'react';
import {
    HeaderButton,
    Panel,
    Button,
    Group,
    PanelHeader,
    Input,
    FormLayout,
    Alert,
    IOS,
    platform
} from '@vkontakte/vkui';
import axios from 'axios';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();

class AddNumber extends React.Component {

    constructor (props){
        super(props);
        this.updateNameValue = this.updateNameValue.bind(this);
        this.updatePhoneValue = this.updatePhoneValue.bind(this);
        this.addNumber = this.addNumber.bind(this);
        this.closePopout = this.closePopout.bind(this);
    }

    state = {
        phone: null,
        name: null,
        alert: null
    };

    updatePhoneValue(evt){
        this.setState({phone: evt.target.value})
    }

    updateNameValue(evt){
        this.setState({name: evt.target.value})
    }

    addNumber() {
        if (this.state.phone && this.state.name){
            if (this.state.phone.length === 10){
                axios.get(
                    `https://emilastanov.pythonanywhere.com/ap_${this.state.phone}/user_${this.state.name}`
                ).then(res=>{
                    if (res.data.status === "SUCCESS"){
                        this.props.alert(
                            <Alert
                                actions={[{
                                    title: 'Ок',
                                    autoclose: true,
                                    style: 'cancel'
                                }]}
                                onClose={this.closePopout}
                            >
                                <h2>Номер добавлен!</h2>
                                <p>Номер успешно довален.</p>
                            </Alert>
                        );
                        this.props.setActivePanel('home')
                    }
                })
            } else {
                this.props.alert(
                    <Alert
                        actions={[{
                            title: 'Ок',
                            autoclose: true,
                            style: 'cancel'
                        }]}
                        onClose={this.closePopout}
                    >
                        <h2>Номер должен состоять из 10 цифр!</h2>
                        <p>9123456789</p>
                    </Alert>
                )
            }
        } else {
            this.props.alert(
                <Alert
                    actions={[{
                        title: 'Ок',
                        autoclose: true,
                        style: 'cancel'
                    }]}
                    onClose={this.closePopout}
                >
                    <h2>Поля не могут быть пустыми!</h2>
                    <p>Оба поля должны быть заполнены.</p>
                </Alert>
            )
        }
    }

    closePopout (){
        this.props.alert(null)
    }

    render() {
        return (
            <Panel id={this.props.id}>
                <PanelHeader
                    left={<HeaderButton onClick={this.props.go} data-to="home">
                        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
                    </HeaderButton>}
                >Добавить</PanelHeader>
                <Group title="Добавить">
                    <FormLayout>
                        <Input top="Телефон" placeholder="9123456789"  value={this.state.phone} onChange={this.updatePhoneValue}/>
                        <Input top="Имя"  value={this.state.name} onChange={this.updateNameValue}/>
                        <Button size="xl" onClick={this.addNumber}>Добавить</Button>
                    </FormLayout>
                </Group>
            </Panel>

        )
    }
}



export default AddNumber;
