import React from 'react';
import { Panel, Button, Group, List, PanelHeader, Input, Cell, FormLayout, Alert} from '@vkontakte/vkui';
import axios from 'axios';

class Home extends React.Component {

	constructor(props) {
		super(props);
		this.searchNumber = this.searchNumber.bind(this);
		this.updateSearchValue = this.updateSearchValue.bind(this);
		this.closePopout = this.closePopout.bind(this);
		this.addNumber = this.addNumber.bind(this);
	}

	state = {
		alert: null,
		searchValue: null,
		resultOfSearch: null
	};

	closePopout () {
		this.props.alert(null);
	}

	searchNumber() {
		if (this.state.searchValue) {
			if (this.state.searchValue.length === 10) {
				axios.get(
					`https://emilastanov.pythonanywhere.com/gp_${this.state.searchValue}`
				).then(res => {
					this.setState({resultOfSearch: res.data.names})
				}).catch(error => {
					if (error.response.status === 500) {
						this.props.alert(<Alert
							actions={[{
								title: 'Нет',
								autoclose: true,
								style: 'cancel'
							}, {
								title: 'Да',
								style: 'default'
							}]}
							onClose={this.closePopout}
							onClick={this.addNumber}
						>
							<h2>Неизвестный номер.</h2>
							<p>Этого номера нет в нашем справочнике. Хотите его добавить?</p>
						</Alert>);

					}
				})
			} else {
				this.props.alert(
					<Alert
						actions={[{
							title: 'Ок',
							autoclose: true,
							style: 'cancel'
						}]}
						onClose={this.closePopout}
					>
						<h2>Неправильный формат номера!</h2>
						<p>9123456789</p>
					</Alert>
				);
			}
		} else {
			this.props.alert(
				<Alert
					actions={[{
						title: 'Ок',
						autoclose: true,
						style: 'cancel'
					}]}
					onClose={this.closePopout}
				>
					<h2>Введите номер!</h2>
					<p>Поле не может быть пустым!</p>
				</Alert>
			);
		}
	}

	addNumber () {
		this.props.alert(
			null
		);
		this.props.setActivePanel('addnumber')
	}

	updateSearchValue (evt) {
		this.setState({
			searchValue: evt.target.value
		})
	}

	render() {
		return (

				<Panel id={this.props.id}>
					<PanelHeader>Телефонный справочник</PanelHeader>
					<Group title="Поиск">
						<FormLayout>
							<Input type="text"  alignment="left" value={this.state.searchValue} onChange={this.updateSearchValue} />
							<Button size="xl" onClick={this.searchNumber}>Поиск</Button>
						</FormLayout>
					</Group>
					{this.state.resultOfSearch ?
						<Group title={this.state.searchValue} style={{'padding': '8px'}}>
							<List>
								{
									this.state.resultOfSearch.map(function (name, id) {
										return <Cell key={id}>{name}</Cell>
									})
								}
							</List>
						</Group>
						:
						null
					}
				</Panel>
		)
	}
}



export default Home;
