import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import AddNumber from "./panels/AddNumber";
import axios from "axios";

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
			fetchedUser: null,
			alert: null,
			userphone: null
		};
		this.addNewUser = this.addNewUser.bind(this)
	}

	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					break;
				case 'VKWebAppGetPhoneNumberResult':
					this.addNewUser( e.detail.data.phone_number.slice(-10) );
					break;
				default:
					console.log(e);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
		connect.send("VKWebAppGetPhoneNumber", {});
	}

	addNewUser(phone) {
		axios.get(
			`https://emilastanov.pythonanywhere.com/gp_${this.state.searchValue}`
		).catch(error => {
			if (error.response.status === 500) {
				axios.get(
					`https://emilastanov.pythonanywhere.com/ap_${phone}/user_${this.state.fetchedUser.first_name} ${this.state.fetchedUser.last_name}`
				).then(res=>{
					console.log('SUCCESS')
				})
			}
		})
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};
	setActivePanel = (panel) => {
		this.setState({ activePanel: panel})
	};

	showAlert = (alert) => {
		this.setState({
			alert: alert
		})
	};

	render() {
		return (
			<View activePanel={this.state.activePanel} popout={this.state.alert}>
				<Home id="home" alert={this.showAlert} fetchedUser={this.state.fetchedUser} go={this.go} setActivePanel={this.setActivePanel}/>
				<AddNumber id="addnumber" alert={this.showAlert} go={this.go} setActivePanel={this.setActivePanel}/>
			</View>
		);
	}
}

export default App;
